.container_movies {
display: grid;
grid-template-columns: repeat(4, 1fr);
}


.single_movie {
    padding: 10px;
    text-align: center;
    border: 3px solid rgba(28,110,164,0.39);
    border-radius: 13px;
    margin: 5px;
    background-color: #f9f8f8;
    cursor: pointer;
    transition: transform 1s;
}
.single_movie:hover{
    transform: scale(1.05,1.05);
}
.single_movie-title{
    min-height: 55px;
}
img{
    width: 150px;
    height: 180px;
    box-shadow: 6px 1px 12px -2px rgba(0,0,0,0.54);
}
.single_movie-vote{
    min-height: 40px;
}

@media (max-width: 695px) {
    .container_movies {
        grid-template-columns: repeat(2, 1fr);
    }
}