body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



* { margin: 0; padding: 0; box-sizing: border-box; font-size: 16px;}


h1 {
    text-align: center;
    font-size: 2rem;
    letter-spacing: 1px;
    font-weight: 300;
}


.navigation {
   display: grid;
   grid-template-columns: 100px 1fr;
    position: -webkit-sticky;
    position: sticky;
    top:0;
    height: 3.5rem;
    min-height: 70px;
    background-color: #2b2d42;
    z-index: 2;
   }
   .navigation_ul{
     display: flex
   
   }
   .navigation_li{
     list-style: none;
     margin: 10px auto;
   }
   
   .navigation__link {
    text-decoration: none;
    color: white;
    padding: 2rem;
   }
   .navigation__link:hover {
  border-bottom: 1px solid white;
   }

   .navigation__logo {
    width: 50px;
    height: 50px;
    border-radius: 50px;
    margin:auto;
    box-shadow:  0 0 10px #000000
  }
   

  @media (max-width: 695px) {
    .navigation {
      display: grid;
      grid-template-columns: 50px 1fr;
       position: -webkit-sticky;
       position: sticky;
       top:0;
       height: 3.5rem;
       min-height: 70px;
       background-color: #2b2d42;
       z-index: 2;
      }
      .navigation_ul{
        display: flex
      
      }
      .navigation_li{
        list-style: none;
        margin:auto;
      }
      
      .navigation__link {
       text-decoration: none;
       color: white;
       padding: 1rem;
       font-size: 0.5rem;
       font-weight: bold;
       letter-spacing: 0.2px;
      }
   
      .navigation__logo {
       width: 30px;
       height: 30px;
       border-radius: 50px;
       margin:auto;
       box-shadow:  0 0 10px #000000
     }
      
  }
.header {
    width: 100%;
    height: 50vh;
    background: url(/movie-app/static/media/avengers.7dcd71c5.jpg);
    opacity: 0.4;
  }
 
  @media (max-width: 695px) {
   .header {
     height: 20vh;
     background-size: cover;
     background-repeat: no-repeat;
     
  }
}
 
.search {
    width: 100%;
    text-align: center;
    margin-top: 20px;
  }
  .search__input {
    margin-left: 1.4rem;
    border: 1px solid transparent;
  }
  .search__input::-webkit-input-placeholder {
    text-align: center;
}
  .search__input::-moz-placeholder {
    text-align: center;
}
  .search__input::-ms-input-placeholder {
    text-align: center;
}
  .search__input::placeholder {
    text-align: center;
}
  
  .search::after {
    content: "";
    display: block;
    height: 1.5px;
    background-color: #a2aaa2;
    width: 80%;
    margin: 5px auto
  }

  @media (max-width: 695px) {
    .search {
      margin-top: 10px;
    }
    .search__input {
      font-size: 0.7rem;
    }
  }
.container_movies {
display: grid;
grid-template-columns: repeat(4, 1fr);
}


.single_movie {
    padding: 10px;
    text-align: center;
    border: 3px solid rgba(28,110,164,0.39);
    border-radius: 13px;
    margin: 5px;
    background-color: #f9f8f8;
    cursor: pointer;
    -webkit-transition: -webkit-transform 1s;
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
}
.single_movie:hover{
    -webkit-transform: scale(1.05,1.05);
            transform: scale(1.05,1.05);
}
.single_movie-title{
    min-height: 55px;
}
img{
    width: 150px;
    height: 180px;
    box-shadow: 6px 1px 12px -2px rgba(0,0,0,0.54);
}
.single_movie-vote{
    min-height: 40px;
}

@media (max-width: 695px) {
    .container_movies {
        grid-template-columns: repeat(2, 1fr);
    }
}
.container_modal {
border: 19px solid #2b2d42;
border-radius: 26px;
display:flex;
flex-direction: column;
max-width: 400px;
margin: auto;
}
.container_modal-title {
    margin: 1.6rem auto
}
.container_modal-description {
    text-align: center;
    margin:auto;
    padding: 20px;

}

.container_modal-button {
    box-shadow:inset 0px 1px 0px 0px #dcecfb;
	background:-webkit-gradient(linear, left top, left bottom, color-stop(5%, #bddbfa), to(#80b5ea));
	background:-webkit-linear-gradient(top, #bddbfa 5%, #80b5ea 100%);
	background:linear-gradient(to bottom, #bddbfa 5%, #80b5ea 100%);
	background-color:#bddbfa;
	border-radius:6px;
	border:1px solid #84bbf3;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:15px;
	font-weight:bold;
	padding:6px 24px;
	text-decoration:none;
    text-shadow:0px 1px 0px #528ecc;
    width: 100px;
    margin: 10px auto;
}

.container_modal-button:hover {
    background:-webkit-gradient(linear, left top, left bottom, color-stop(5%, #80b5ea), to(#bddbfa));
    background:-webkit-linear-gradient(top, #80b5ea 5%, #bddbfa 100%);
    background:linear-gradient(to bottom, #80b5ea 5%, #bddbfa 100%);
	background-color:#80b5ea;
}
.trending_title {
font-size: 1.6rem;
text-align: center;
}

.trending_container-movies {
margin-top: 10px;
display: grid;
grid-template-columns: 1fr 1fr
}

.trending_container {
margin: 10px auto
}

.trending_movieName {
padding: 5px;
text-align: center;
}

.trending_img {
    margin: 20px auto;
}

.actors_input-container{
  text-align: center;
  margin-top: 10px;
}
.actors_input {
    margin:auto;
    border: 1px solid transparent;
    border-bottom: 1px solid #a2aaa2;;
}
.actors_input::-webkit-input-placeholder {
    text-align: center;
}
.actors_input::-moz-placeholder {
    text-align: center;
}
.actors_input::-ms-input-placeholder {
    text-align: center;
}
.actors_input::placeholder {
    text-align: center;
}

.container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}
.actor_container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 15% 70% 10%;
    width: 300px;
    height: 300px;
    border: 1.5px solid gray;
    margin: 10px;
    border-radius: 20px;
}
.actor_name{
grid-column: 1/3;
text-align: center;
}
.actor_name::after {
    content: "";
    display: block;
    height: 0.8px;
    width: 60%;
    background-color: gray;
    margin:5px auto;
}

.actor_ul-list {
   display:flex;
   flex-direction: column;
   
}

.actor_list{
    list-style: none;
    padding: 5px;
    text-align: center;   
    justify-content: space-between;
}


.actor_img {
grid-row: 2/3;
width:140px;
border-radius: 20px;
margin: auto;

}


.actor_popularity {
    grid-column:1/3;
    text-align: center;
    align-self: flex-end;
    
}
.actor_popularity::before {
    content: "";
    display: block;
    height: 0.8px;
    width: 60%;
    background-color: gray;
    margin:5px auto;
}

@media (max-width: 695px) {
    .container {
        grid-template-columns: 1fr;
        width: 100%;
    }
.actor_container{
    width: 90%;

}
    
}
footer {
    display: grid;
    grid-template-columns: 100px 1fr;
    background-color: #2b2d42;
    margin-top: 30px;
    min-height: 120px;
}

.footer_github {
    
    width: 30px;
    height: 30px;
    margin: auto;
}
.footer_list{
    display: flex;
}
.footer_element-li{
    list-style: none;
    margin: auto;
}
.footer_link {
    text-decoration: none;
    color:white
}
.footer_link:hover{
    border-bottom: 1px solid white;
}
